.boletos{
    padding:$separacion 0;

    h3{
        text-align: center;
        color: $blanco;
    }

    .contenedor-boletos{
        margin-top: $separacion/2;
        @include grid(2, $separacion);
    }

    .contenedor-boletos p{
        color: $blanco;
        font-size: 3rem;
        
        text-align: center;
        margin-bottom: 2rem;

      
    }
}

.pase{
    width: 100%;
    border-spacing: 0;

    thead{
        background-color: $rosa;
        th{
            padding: $separacion/3;
            color: $blanco;
            text-transform: uppercase;
            font-weight: $delgada;
            font-size: 2rem;
        }
    }

    tbody{
         background-color: $blanco;
         td{
             padding: $separacion/4;
            border-bottom: 1px solid #e1e1e1;
           
         }
         tr{
            &:last-of-type{
                border: none;
            }
         }
    }

    tfoot{
        background-color: $morado;
        padding: $separacion/4;
        text-align: center;
        font-size: 3rem;
        color: $blanco;

    }
}